<template>
  <div class="app-container public-table">
    <div class="group-header">
      <el-select v-model="selectedPermit" filterable placeholder="选择或搜索权限组" @change="changePermit" value-key="groupid" style="width: 100%">
        <el-option v-for="item in permitList" :key="item.groupid" :label="item.name" :value="item">
        </el-option>
      </el-select>
    </div>
    <el-table :data="moduleList" border v-loading="loading" height="calc(100% - 160px)">
      <el-table-column label="权限组" align="center">
        <template slot-scope="scope">
          <span>{{ selectedPermit.name ? selectedPermit.name : '未选择' }}</span>
        </template>
      </el-table-column>
      <el-table-column label="模块名" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.zh_name }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-switch @change="resetPermit(scope.$index)" v-model="scope.row.isSwitch" active-color="#13ce66" inactive-color="#ff4949">
          </el-switch>
        </template>
      </el-table-column>
    </el-table>
    <el-button type="primary" style="width: 300px;margin-top:20px;" @click="createdPermit">新建一个权限组</el-button>
  </div>
</template>

<script>
import { fetchData } from '@/api'
export default {
  data() {
    return {
      loading: false,
      selectedPermit: {},
      permitList: [],
      moduleList: []
    }
  },
  methods: {
    // 获取所有权限组
    fetchPermitGroups() {
      fetchData('fetchPermitGroup').then(data => {
        if (data) this.permitList = data
      })
    },
    // 获取模块列表
    fetchModuleList() {
      fetchData('fetchAllModule').then(data => {
        if (data) {
          data.map(v => v.isSwitch = false)
          this.moduleList = data
        }
      })
    },
    // 选择权限组
    changePermit(obj) {
      this.loading = true
      this.moduleList.map(v => v.isSwitch = false)
      fetchData('fetchPermitModule', { groupid: obj.groupid }).then(data => {
        this.loading = false
        if (!data || !data.length) {
          return false
        }
        data.forEach(elm => {
          if (elm.module === '*') {
            this.moduleList.map(v => v.isSwitch = true)  // 选中所有模块
          } else {
            this.moduleList.map(v => {
              if (v.module_name === elm.module) v.isSwitch = true
            })
          }
        })
      })
    },
    // 修改权限组
    resetPermit(index) {
      if (this.selectedPermit.groupid) {
        const nowModule = this.moduleList[index]
        nowModule.isSwitch ? this.addPermitModule(nowModule.module_name, index) : this.removePermitModule(nowModule.module_name, index)
      }
    },
    // 添加权限组模块
    addPermitModule(name, index) {
      fetchData('addPermitModule', {
        groupid: this.selectedPermit.groupid,
        module: name,
        actions: '*'
      }).then(data => {
        data ? ELEMENT.Message.success('操作成功') : this.moduleList[index].isSwitch = false
      })
    },
    // 移除权限组模块
    removePermitModule(name, index) {
      fetchData('removePermitModule', {
        groupid: this.selectedPermit.groupid,
        module: name
      }).then(data => {
        data ? ELEMENT.Message.success('操作成功') : this.moduleList[index].isSwitch = false
      })
    },
    // 新建一个权限组
    createdPermit() {
      ELEMENT.MessageBox.prompt('请输入权限组名称', '新建权限组', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(({ value }) => {
        if (value) {
          fetchData('newPermitGroup', {
            parentid: '',
            name: value,
            desc: value
          }).then(data => {
            if (data) {
              ELEMENT.Message.success('操作成功')
              this.fetchPermitGroups()
            }
          })
        } else {
          ELEMENT.Message.error('名称不符合要求')
        }
      }).catch(Err => console.log(Err))
    }
  },
  created() {
    this.fetchModuleList()
    this.fetchPermitGroups()
  }
}
</script>

<style lang="less" scoped>
.group-header {
  margin-bottom: 20px;
}
</style>





